<template>
  <div>
    <input type="text" v-model="input" placeholder="请输入教师姓名" />
    <el-button
      class="search"
      slot="append"
      icon="el-icon-search"
      @click="search(input)"
    ></el-button>
    <!-- 头部 -->
    <div class="addButton">
      <button @click="handleAdd">添加教师</button>
      <button @click="handleExcel">Excel导入</button>
    </div>
    <!-- 添加时的弹框 -->
    <el-dialog
      class="addDialog"
      title="教师信息"
      :visible.sync="dialogVisible1"
      width="52%"
      :before-close="handleClose"
    >
      <!-- 用户的所需要填写的表单信息 -->
      <el-form
        ref="form"
        :rules="rules"
        :model="form"
        :inline="true"
        label-width="80px"
      >
        <el-form-item label="教师姓名" prop="name">
          <el-input placeholder="请输入教师姓名" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex" placeholder="请选择">
            <el-option label="男" :value="0"></el-option>
            <el-option label="女" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国籍" prop="identity">
          <el-select v-model="form.identity" placeholder="请选择">
            <el-option label="俄罗斯" :value="0"></el-option>
            <el-option label="中国" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input placeholder="请输入年龄" v-model="form.age"></el-input>
        </el-form-item>
        <el-form-item label="职位" prop="position">
          <el-select v-model="form.position" placeholder="请选择">
            <el-option label="外教" :value="2"></el-option>
            <el-option label="助教" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="职称" prop="title">
          <el-select v-model="form.title" placeholder="请选择">
            <el-option label="教授" :value="1"></el-option>
            <el-option label="副教授" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="专业" prop="major">
          <el-select v-model="form.major" placeholder="请选择">
            <el-option label="计算机科学与技术" :value="0"></el-option>
            <el-option label="自动化" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input placeholder="请输入邮箱" v-model="form.email"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button @click="submit">确 定 </el-button>
      </span>
    </el-dialog>
    <!-- excel导入弹框 -->
    <el-dialog
      class="addDiaglog"
      title="Excel导入"
      :visible.sync="dialogVisible2"
      width="40%"
    >
      <el-upload
        class="upload-demo"
        action="#"
        :before-upload="beforeUpload"
        accept=".xls, .xlsx"
        :before-remove="beforeRemove"
        :multiple="false"
        :limit="1"
        :on-exceed="handleExceed"
        :file-list="fileList"
      >
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">
          只能上传xls/xlsx文件，且不超过500kb
          <a @click="downloadFile">下载模板</a>
        </div>
      </el-upload>
    </el-dialog>
    <!-- 展示表单 -->
    <div class="common-table">
      <el-table
        height="90%"
        :data="pageTicket"
        style="width: 100%"
        stripe
        empty-text="暂无数据"
      >
        <el-table-column prop="index" label="#" min-width="6.4%">
          <template slot-scope="scope">
            <span>{{ scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="教师姓名" min-width="14%">
        </el-table-column>
        <el-table-column prop="sex" label="性别" min-width="6.4%">
          <template slot-scope="scope">
            <span>{{ scope.row.sex == 1 ? "女" : "男" }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="age" label="年龄" min-width="6.4%">
        </el-table-column>
        <el-table-column prop="position" label="职位" min-width="6.4%">
          <template slot-scope="scope">
            <span>{{ scope.row.position == 1 ? "助教" : "外教" }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="identity" label="国籍" min-width="9.4%">
          <template slot-scope="scope">
            <span>{{ scope.row.identity == 1 ? "中国" : "俄罗斯" }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="title" label="职称" min-width="6.4%">
          <template slot-scope="scope">
            <span>{{ scope.row.title == 1 ? "副教授" : "教授" }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="major" label="专业" min-width="17%">
          <template slot-scope="scope">
            <span>{{
              scope.row.major == 1 ? "自动化" : "计算机科学与技术"
            }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="email" label="邮箱" min-width="17%">
        </el-table-column>
        <el-table-column label="操作" min-width="17%">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >编辑
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 尾部 -->
    <div class="footer">
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentpage"
          :page-size="pagesize"
          layout="total, prev, pager, next, jumper"
          :total="tableData.length"
        >
        </el-pagination>
      </div>
      <button class="reset" @click="reset">重置</button>
    </div>
  </div>
</template>

<script>
import {
  downLoad,
  getTeacherMessage,
  uploadExcel,
  deleteTeacher,
  editTeacher,
} from "../utils/api";
import { addTeacher } from "../utils/api";
export default {
  name: "TeacherManage",
  data() {
    return {
      input: "",
      //上传文件所用的token
      dialogVisible1: false, //教师弹窗
      dialogVisible2: false, //excel弹窗
      fileList: [],
      form: {
        age: "",
        email: "",
        identity: "",
        major: "",
        name: "",
        position: "",
        sex: "",
        title: "",
      },
      modalType: 0, // 0表示新增的弹窗，1表示编辑
      rules: {
        name: [{ required: true, message: "请输入姓名" }],
        age: [{ required: true, message: "请输入年龄" }],
        sex: [{ required: true, message: "请选择性别" }],
        email: [
          { required: true, message: "请输入邮箱" },
          { validator: this.checkEmail, trigger: "blur" },
        ],
        title: [{ required: true, message: "请选择职称" }],
        major: [{ required: true, message: "请选择专业" }],
        position: [{ required: true, message: "请选择职位" }],
        identity: [{ required: true, message: "请选择国籍" }],
      },
      tableData: [],
      currentpage: 1, //当前所在页默认是第一页
      pagesize: 10, //每页显示多少行数据 默认设置为10
      pageTicket: [], //分页后的当前页数据
    };
  },
  methods: {
    //用来检查email的合法性
    checkEmail(rule, value, cb) {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
      if (regEmail.test(value)) {
        //合法的邮箱
        return cb();
      }
      //邮箱不合法
      cb(new Error("请输入合法的邮箱！"));
    },
    //搜索
    search(input) {
      this.pageTicket = this.tableData.filter((data) => {
        return data.name === input;
      });
    },
    handleExcel() {
      this.dialogVisible2 = true;
    },
    beforeUpload: function (file) {
      var fd = new window.FormData();
      fd.append("file", file);
      uploadExcel(fd).then((res) => {
        if (res.data.code == 0) {
          this.$message.success("上传文件成功！");
        } else {
          this.$message.error("上传文件失败！");
        }
      });
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${
          files.length + fileList.length
        } 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    //弹窗确认事件
    submit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.modalType === 0) {
            addTeacher(this.form)
              .then((res) => {
                if (res.data.code == 0) {
                  this.$message.success("添加成功！");
                  this.getList();
                  this.dialogVisible1 = false;
                  this.$refs.form.resetFields();
                } else {
                  this.$message.error("添加失败！");
                }
              })
              .catch((err) => {
                console.log(err);
              });
          } else {
            console.log(this.form, "1");
            if (this.form.position === "外教") {
              this.form.position = 2;
            } else {
              this.form.position = 1;
            }
            if (this.form.title === "教授") {
              this.form.title = 1;
            } else {
              this.form.title = 2;
            }
            editTeacher({
              id: this.form.id,
              name: this.form.name,
              sex: this.form.sex,
              age: this.form.age,
              position: this.form.position,
              pTitle: this.form.title,
              major: this.form.major,
              email: this.form.email,
              identity: this.form.identity,
            })
              .then((res) => {
                if (res.data.code === 0) {
                  this.$message.success("编辑成功！");
                  this.getList();
                  this.dialogVisible1 = false;
                  this.$refs.form.resetFields();
                } else {
                  this.$message.error("编辑失败");
                  console.log(res.data);
                  console.log(this.form);
                }
              })
              .catch((err) => {
                console.log(err);
              });
          }
        }
      });
    },
    //弹窗取消
    cancel() {
      this.handleClose();
    },
    //新增教师按钮
    handleAdd() {
      this.modalType = 0;
      this.dialogVisible1 = true;
    },
    //下载模板
    downloadFile() {
      downLoad().then((res) => {
        console.log(res.data,'res.data');
        const blob = new Blob([res.data], {
          type: "application/vnd.ms-excel",
        });
        const link = document.createElement("a"); // 生成一个a标签。
        link.href = window.URL.createObjectURL(blob); // href属性指定下载链接
        link.download = "教师下载模板"; // dowload属性指定文件名
        link.click(); // click()事件触发下载
      });
    },
    //关闭教师按钮
    handleClose() {
      this.dialogVisible1 = false;
      this.$refs.form.resetFields();
      // console.log(this.form);
    },
    //删除按钮
    handleDelete(row) {
      this.$confirm("确定要删除这条信息吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        deleteTeacher({ id: row.id })
          .then((res) => {
            if (res.data.code === 0) {
              this.$message.success("删除成功！");
              this.tableData = this.tableData.filter((item) => {
                return item.id !== row.id;
              });
              this.getPageInfo();
            } else {
              this.$message.error("删除失败！");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },
    //编辑按钮
    handleEdit(row) {
      this.modalType = 1;
      this.dialogVisible1 = true;
      this.$nextTick(() => {
        this.form = JSON.parse(JSON.stringify(row));
      });
    },
    //获取列表信息
    getList() {
      getTeacherMessage()
        .then((res) => {
          if (res.data.code === 0) {
            this.tableData = res.data.data;
            this.getPageInfo();
          } else {
            this.$message.error("获取列表信息错误！");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getPageInfo() {
      //清空pageTicket中的数据
      this.pageTicket = [];
      // 获取当前页的数据
      for (
        let i = (this.currentpage - 1) * this.pagesize;
        i < this.tableData.length;
        i++
      ) {
        //把遍历的数据添加到pageTicket里面
        this.pageTicket.push(this.tableData[i]);
        //判断是否达到一页的要求
        if (this.pageTicket.length === this.pagesize) break;
      }
    },
    handleSizeChange(size) {
      //修改当前每页的数据行数
      this.pagesize = size;
      //数据重新分页
      this.getPageInfo();
    },
    handleCurrentChange(pageNumber) {
      //修改当前的页码
      this.currentpage = pageNumber;
      //数据重新分页
      this.getPageInfo();
    },
    //重置按钮
    reset() {
      this.getList();
      this.input = "";
    },
  },
  mounted() {
    this.getList();
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
};
</script>

<style scoped>
/* 搜索框 */
input {
  position: absolute;
  width: 606.93px;
  height: 37.5px;
  left: 308.15px;
  top: 40.62px;
  box-sizing: border-box;
  background: rgb(247, 247, 247);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
  outline-color: transparent;
  color: rgb(127, 127, 127);
  font-family: Noto Sans SC;
  padding-left: 34.38px;
  line-height: 37.5px;
  font-size: 12px;
}
.search {
  position: absolute;
  background:transparent;
  border: none;
  left: 870.46px;
  top: 40.62px;
  height: 37.5px;
  border-top-right-radius: 6.25px;
  border-bottom-right-radius: 6.25px;
  color: rgb(127, 127, 127);
}
.search:active {
  color: rgb(127, 127, 127);
  border: none;
  outline: 0;
}
.search:focus,
.search:hover {
  color: rgb(127, 127, 127);
  border: none;
  background-color: transparent;
}
.addButton {
  display: flex;
  position: absolute;
  top: 40.62px;
  left: 951.97px;
  right: 154.39px;
}
.addButton button {
  height: 37.5px;
  margin-right: 25px;
  flex: 0 0 93.76px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgb(42, 96, 189);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
}
/* 弹窗 */
.el-select {
  width: 208px !important;
}
.dialog-footer .el-button {
  background-color: rgb(42, 96, 189);
  color: rgb(255, 255, 255);
}
a {
  float: right;
  border-bottom: 1px dotted red;
  color: rgb(42, 96, 189);
}
/* 展示表格部分 */
.common-table {
  position: relative;
  height: 459px;
  top: 48px;
}
.el-table {
  font-size: 12px;
}
.el-table >>> .el-table__header {
  height: 37.5px !important;
}
.el-table >>> .el-table__cell {
  padding: 0;
}
.el-table >>> .el-table__row {
  height: 37.5px !important;
}
.reset {
  position: absolute;
  width: 60px;
  height: 27px;
  bottom: 35px;
  right: 38px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgb(42, 96, 189);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
}
/* 分页 */
.block {
  margin-top: 18px;
}
.el-pagination {
  text-align: center;
  width: 24rpx;
  height: 24rpx;
  position: absolute;
  left: 22rpx;
  border-left: 4rpx solid #ffffff;
  border-bottom: 4rpx solid #ffffff;
}
.el-pagination::before {
  content: "";
  position: absolute;
  top: -20rpx;
  left: -20rpx;
  bottom: -20rpx;
  right: -20rpx;
}
</style>