import { request } from './baseURL' // 引入刚刚创建的域名文件
import Cookie from 'js-cookie'
export function getRussiaTeacher() {
    return request({
      url: '/api/teacher/get/russia',
      headers: {
        'Content-Type': 'application/json',
        token: Cookie.get('token')
      },
      method: 'get'
    })
}
export function getChineseTeacher() {
    return request({
      url: '/api/teacher/get/china',
      headers: {
        'Content-Type': 'application/json',
        token: Cookie.get('token')
      },
      method: 'get'
    })
}
export function getPublishName() {
    return request({
      url: '/api/evaluate/get/name',
      headers: {
        'Content-Type': 'application/json',
        token: Cookie.get('token')
      },
      method: 'get'
    })
}
export function getSubmitList() {
    return request({
      url: '/api/email/get/info',
      headers: {
        'Content-Type': 'application/json',
        token: Cookie.get('token')
      },
      method: 'get'
    })
}
export function submitEmail(data) {
  return request({
    url: '/api/email/send',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'post',
    data: data
  })
}


