<template>
  <div>
    <div class="submit" @click="submit">提交</div>
    <div class="chooseName">
      <el-select v-model="value" placeholder="请选择评价名称">
        <el-option
          v-for="item in options"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        >
        </el-option>
      </el-select>
    </div>
    <div class="ChinesePostObj">
      <!-- 下拉有多选功能 -->
      <el-dropdown trigger="click" class="dropdown">
        <span class="el-dropdown-link">
          请选择中方邮件发送对象<i
            class="el-icon-arrow-down el-icon--right"
          ></i>
        </span>
        <el-dropdown-menu
          slot="dropdown"
          :hide-on-click="false"
          class="ChineseObj"
        >
          <div class="drop">
            <el-checkbox
              :indeterminate="isIndeterminate1"
              v-model="checkAll1"
              @change="handleCheckAllChange1"
              class="checkAlls"
            >
              全选
            </el-checkbox>
            <div class="checkAllLine"></div>
            <el-checkbox-group
              v-model="isCheckIdList1"
              v-if="checkboxLists1.length > 0"
              @change="handleCheckedChange1"
            >
              <div class="checkboxLists">
                <el-checkbox
                  v-for="item in checkboxLists1"
                  :label="item.id"
                  :key="item.id"
                  style="display: block"
                  class="checiboxItem"
                >
                  {{ item.name }}
                </el-checkbox>
              </div>
            </el-checkbox-group>
            <div v-if="checkboxLists1.length === 0" class="noData">
              暂无数据
            </div>
          </div>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="RussiaPostObj">
      <el-dropdown trigger="click" class="dropdown">
        <span class="el-dropdown-link">
          请选择俄方邮件发送对象<i
            class="el-icon-arrow-down el-icon--right"
          ></i>
        </span>
        <el-dropdown-menu
          slot="dropdown"
          :hide-on-click="false"
          class="RussiaObj"
        >
          <div class="drop">
            <el-checkbox
              :indeterminate="isIndeterminate2"
              v-model="checkAll2"
              @change="handleCheckAllChange2"
              class="checkAlls"
            >
              全选
            </el-checkbox>
            <div class="checkAllLine"></div>
            <el-checkbox-group
              v-model="isCheckIdList2"
              v-if="checkboxLists2.length > 0"
              @change="handleCheckedChange2"
            >
              <div class="checkboxLists">
                <el-checkbox
                  v-for="item in checkboxLists2"
                  :label="item.id"
                  :key="item.id"
                  style="display: block"
                  class="checiboxItem"
                >
                  {{ item.name }}
                </el-checkbox>
              </div>
            </el-checkbox-group>
            <div v-if="checkboxLists2.length === 0" class="noData">
              暂无数据
            </div>
          </div>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="ChineseClock">
      <el-date-picker
        v-model="CClock"
        type="datetime"
        placeholder="请选择中方邮件发送时间"
        value-format="yyyy-MM-dd HH-mm-ss"
        format="yyyy-MM-dd HH-mm-ss"
      >
      </el-date-picker>
    </div>
    <div class="RussiaClock">
      <el-date-picker
        v-model="RClock"
        type="datetime"
        placeholder="请选择俄方邮件发送时间"
        value-format="yyyy-MM-dd HH-mm-ss"
        format="yyyy-MM-dd HH-mm-ss"
      >
      </el-date-picker>
    </div>
    <i class="el-icon-refresh" @click="refresh"></i>
    <el-table :data="tableData" stripe style="width: 75%; height: 35%">
      <el-table-column prop="submitData" label="提交记录" min-width="100%">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {
  getChineseTeacher,
  getRussiaTeacher,
  getPublishName,
  getSubmitList,
  submitEmail,
} from "../utils/opinionApi";
export default {
  name: "Opinion",
  data() {
    return {
      options: [],
      value: "", //最长的选择
      checkAll1: false, //是否全选
      isIndeterminate1: false, //不是全选也不是全不选
      isCheckIdList1: [], //选中的
      checkboxLists1: [], //所有的
      checkAll2: false, //是否全选
      isIndeterminate2: false, //不是全选也不是全不选
      isCheckIdList2: [], //选中的
      checkboxLists2: [], //所有的
      CClock: "",
      RClock: "",
      tableData: [], //提交的记录
    };
  },
  methods: {
    submit() {
      console.log(typeof this.CClock);
      submitEmail({
        eid: this.options.id,
        chineseTeacherId: this.isCheckIdList1,
        chineseTime: this.CClock,
        russianTeacherId: this.isCheckIdList2,
        russianTime: this.RClock,
      }).then((res) => {
        console.log(res.data);
      });
    },
    handleCheckAllChange1(val) {
      let idList = this.checkboxLists1.map((x) => x.id);
      this.isCheckIdList1 = val ? idList : [];
      this.isIndeterminate1 = false;
    },
    handleCheckedChange1(value) {
      let checkedCount = value.length;
      this.checkAll1 = checkedCount === this.checkboxLists1.length;
      this.isIndeterminate1 =
        checkedCount > 0 && checkedCount < this.checkboxLists1.length;
    },
    handleCheckAllChange2(val) {
      let idList = this.checkboxLists2.map((x) => x.id);
      this.isCheckIdList2 = val ? idList : [];
      this.isIndeterminate2 = false;
    },
    handleCheckedChange2(value) {
      let checkedCount = value.length;
      this.checkAll2 = checkedCount === this.checkboxLists2.length;
      this.isIndeterminate2 =
        checkedCount > 0 && checkedCount < this.checkboxLists2.length;
    },
    refresh() {
      this.$message.success("刷新成功");
      this.getSubmit();
    },
    getSubmit() {
      getSubmitList().then((res) => {
        const getData = res.data.data;
        this.tableData = getData.map((item) => {
          return {
            submitData: item.name + "在" + item.submit_time + item.operation,
          };
        });
      });
    },
  },
  mounted() {
    //获取俄方教师信息
    getRussiaTeacher().then((res) => {
      this.checkboxLists2 = res.data.data;
    });
    getChineseTeacher().then((res) => {
      this.checkboxLists1 = res.data.data;
    });
    getPublishName().then((res) => {
      this.options = res.data.data;
    });
    this.getSubmit();
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
};
</script>

