//引入VueRouter
import VueRouter from 'vue-router'

//引入路由组件
import Login from '../views/Login'
import Home from '../views/Home'
import Charts from '../views/Charts'
import ClassManage from '../views/ClassManage'
import TeacherManage from '../views/TeacherManage'
import StudentManage from '../views/StudentManage'
import Publish from '../views/Publish'
import Score from '../views/Score'
import Opinion from '../views/Opinion'
import Comment from '../views/Comment'
import Table from '../views/Table'


export default new VueRouter({
    routes: [
        { path: '/', component: Login, name: 'Login' },
        {
            path: '/home', redirect: '/table', component: Home, name: 'Home', children: [
                { path: '/table', component: Table, name: 'Table' },
                { path: 'teacherManage', component: TeacherManage, name: 'TeacherManage' },
                { path: 'studentManage', component: StudentManage, name: 'StudentManage' },
                { path: 'classManage', component: ClassManage, name: 'ClassManage' },
                { path: 'publish', component: Publish, name: 'Publish' },
                { path: 'score', component: Score, name: 'Score' },
                { path: 'opinion', component: Opinion, name: 'Opinion' },
                { path: 'comment', component: Comment, name: 'Comment' },
                { path: 'charts', component: Charts, name: 'Charts' },
            ]
        },
    ]
})

