<template>
  <div class="login">
    <img class="logo" src="../assets/images/logo.png" alt="" />
    <!-- 主体部分 -->
    <el-form ref="form" class="login-container" :model="form">
      <div class="title">
        <h2>学评教系统</h2>
        <h3>管理端</h3>
      </div>
      <div class="form">
        <el-form-item prop="username">
          <span class="form-label">账号:</span>
          <input
            v-model="form.username"
            placeholder="请输入账号"
            id="username"
            label="账号："
          />
        </el-form-item>
        <el-form-item style="margin-top: 0.4rem" prop="password">
          <span class="form-label">密码:</span>
          <input
            type="password"
            v-model="form.password"
            placeholder="请输入密码"
            id="password"
          />
        </el-form-item>
      </div>
      <el-form-item> </el-form-item>
    </el-form>
    <button @click="submit">登录</button>
  </div>
</template>

<script>
import { getUser } from "../utils/api.js";
import Cookie from "js-cookie";
export default {
  name: "Login",
  data() {
    return {
      form: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    submit() {
      getUser(this.form)
        .then((res) => {
          console.log(res.data);
          if (res.data.code === 0) {
            Cookie.set("token", res.data.data);
            this.$router.push("/home");
          } else {
            this.$message.error("密码或账号输入错误！");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

