import { request } from './baseURL' // 引入刚刚创建的域名文件
import Cookie from 'js-cookie';
//学生上传文件
export function uploadExcel(data) {
    return request({
        url: '/api/student/excel/import',
        headers: {
            "Content-Type": "multipart/form-data",
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//添加学生的按钮
export function addStudent(data) {
    return request({
        url: '/api/student/add',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//编辑学生信息
export function editStudent(data) {
    return request({
        url: '/api/student/update',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//学生信息excel导出
export function downLoad() {
    return request({
        url: '/api/student/excel/export',
        responseType: "blob",
        headers: {
            "Content-Type": "application/json",
            token: Cookie.get("token"),
        },
        method: 'get',
    })
}
//删除学生信息
export function deleteStudent(data) {
    return request({
        url: '/api/student/delete',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//获取学生信息
export function getStudentMessage() {
    return request({
        url: '/api/student/list',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'get'
    })
}