import { request } from './baseURL' // 引入刚刚创建的域名文件
import Cookie from 'js-cookie'

//四个扇形图
export function getBasicData() {
  return request({
    url: '/api/charts/get/static',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'get'
  })
}
//红线教师数据
export function getRedline() {
  return request({
    url: '/api/charts/get/teacher/redline',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'get'
  })
}
//表格数据
export function getTableData(data) {
  return request({
    url: '/api/charts/get/rank/chart',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'post',
    data: data
  })
}
