<template>
  <div class="chart">
    <div class="russianpro">
      <h2>俄方教师基本信息</h2>
      <div
        ref="russianChart"
        style="width: 12rem; height: 8rem; top: 0.5rem"
      ></div>
      <div ref="russianChart2" style="width: 12rem; height: 8rem"></div>
    </div>
    <div class="chinesepro">
      <h2>中方教师基本信息</h2>
      <div
        ref="chineseChart"
        style="position: absolute; width: 12rem; height: 8rem; bottom: 8.5rem"
      ></div>
      <div
        ref="chineseChart2"
        style="position: absolute; width: 12rem; height: 8rem; bottom: 2rem"
      ></div>
    </div>
    <div class="chineseRadarChart">
      <h2>中方教师指标雷达图</h2>
      <el-dropdown trigger="click" class="chineseDropdown">
        <span class="el-dropdown-link">
          请选择中方教师<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu
          slot="dropdown"
          :hide-on-click="false"
          class="ChineseObj"
        >
          <el-dropdown-item
            v-for="item of chineseTeacher"
            :key="item.id"
            @click.native="clickpro(item.id)"
          >
            <span>{{ item.name }}</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div
        ref="chineseRadarChar"
        style="
          width: 15rem;
          height: 15rem;
          position: absolute;
          top: 45px;
          left: -40px;
        "
      ></div>
    </div>
    <div class="russianRadarChart">
      <h2>俄方教师指标雷达图</h2>
      <el-dropdown trigger="click" class="russianDropdown">
        <span class="el-dropdown-link">
          请选择俄方教师<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu
          slot="dropdown"
          :hide-on-click="false"
          class="RussianObj"
        >
          <el-dropdown-item
            v-for="item of russianTeacher"
            :key="item.id"
            @click.native="clickpro2(item.id)"
          >
            <span>{{ item.name }}</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div
        ref="russianRadarChar"
        style="
          width: 15rem;
          height: 15rem;
          position: absolute;
          top: 35px;
          left: -5px;
        "
      ></div>
    </div>
    <div class="redline">
      <h2>教师红线名单</h2>
      <div class="content">
        <div class="content-item" v-for="item in redlineArr" :key="item">
          {{ item }}
        </div>
      </div>
    </div>
    <div class="ranking">
      <h2>教师分数排名<br />前十名</h2>
      <el-switch
        v-model="country"
        active-color="rgba(255, 226, 228, 0.75)"
        inactive-color="rgba(224, 239, 255, 0.75)"
        :active-value="1"
        :inactive-value="0"
        active-text="中"
        inactive-text="俄"
        @change="handleCountryBtn"
      >
      </el-switch>
      <el-table
        :data="tableData"
        height="30%"
        style="width: 100%; font-size: 8px"
        class="tableData"
        :row-style="{ height: '0' }"
        :cell-style="{ padding: '0' }"
        :header-row-style="{ height: '0' }"
        :header-cell-style="{ padding: '0' }"
      >
        <el-table-column prop="index" label="#" min-width="20%">
          <template slot-scope="scope">
            <span>{{ scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="teacherName" label="教师姓名" min-width="50">
        </el-table-column>
        <el-table-column prop="score" label="分数" min-width="30%">
        </el-table-column>
      </el-table>
      <div
        ref="rankingChart"
        style="position: absolute; width: 16rem; height: 12rem; top: 11rem"
      ></div>
    </div>
    <div class="target">
      <h2>教师指标柱状图</h2>
      <el-dropdown trigger="click" class="teacherDropdown">
        <span class="el-dropdown-link">
          请选择教师<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu
          slot="dropdown"
          :hide-on-click="false"
          class="TeacherObj"
        >
          <el-dropdown-item
            v-for="item of teacher"
            :key="item.id"
            @click.native="clickteacher(item.id)"
          >
            <span>{{ item.name }}</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-dropdown trigger="click" class="termDropdown">
        <span class="el-dropdown-link">
          请选择评测<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu
          slot="dropdown"
          :hide-on-click="false"
          class="TermObj"
        >
          <el-dropdown-item
            v-for="item of testName"
            :key="item.id"
            @click.native="clickTerm(item.id)"
          >
            <span>{{ item.name }}</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div
        ref="targeting"
        style="
          position: absolute;
          width: 18rem;
          height: 8rem;
          top: 4.1rem;
          left: -1rem;
        "
      ></div>
    </div>
    <div class="predict">
      <h2>教师总分预测折线图</h2>
      <el-dropdown trigger="click" class="teacherDropdown">
        <span class="el-dropdown-link">
          请选择教师<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu
          slot="dropdown"
          :hide-on-click="false"
          class="TeacherObj"
        >
          <el-dropdown-item
            v-for="item of teacher"
            :key="item.id"
            @click.native="clickteacher(item.id)"
          >
            <span>{{ item.name }}</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div
        ref="sumPredict"
        style="
          position: absolute;
          width: 25rem;
          height: 8rem;
          top: 2.9rem;
          left: -4rem;
        "
      ></div>
    </div>
  </div>
</template>

<script>
import {
  getRedline,
  getTableData,
  getChineseTeacher,
  getRussianTeacher,
  getRadar,
  getBasicData,
  getTestName,
  getTargeting,
} from "../utils/chartApi";
import * as echarts from "echarts";
export default {
  name: "Charts",
  data() {
    return {
      russianChart1: null, //扇形图1
      russianChart2: null, //扇形图2
      chineseChart1: null, //扇形图1
      chineseChart2: null, //扇形图2
      rangkingChart: null, //分数排名图1
      targeting: null, //分数指标指标柱状图
      chineseTeacher: [], //雷达图选择中国教师
      russianTeacher: [], //雷达图选择俄罗斯教师
      teacher: [], //请选择教师的个数
      chineseRadarArr: [], //雷达图中国数组
      chineseRadarChar: {}, //中国图表
      chineseRadarCharOption: {},
      russianRadarArr: [],
      russianRadarChar: {},
      russianRadarCharOption: {},
      redlineArr: [], //红线名单
      tableData: [],
      testName: [],
      country: 1,
      testID: null, //教师指标柱状图选择的评测
    };
  },
  methods: {
    //雷达选择1
    clickpro(val) {
      getRadar({ id: val }).then((res) => {
        for (var i = 0; i < res.data.data.length; i++) {
          this.chineseRadarArr.push(res.data.data[i].systemScore);
        }
        this.chineseRadarChar.clear();
        this.chineseRadarCharOption.series[0].data[0].value =
          this.chineseRadarArr;
        this.chineseRadarChar.setOption(this.chineseRadarCharOption, true);
      });
    },
    //雷达选择2
    clickpro2(val) {
      getRadar({ id: val }).then((res) => {
        for (var i = 0; i < res.data.data.length; i++) {
          this.russianRadarArr.push(res.data.data[i].systemScore);
        }
        this.russianRadarChar.clear();
        this.russianRadarCharOption.series[0].data[0].value =
          this.russianRadarArr;
        this.russianRadarChar.setOption(this.russianRadarCharOption, true);
      });
    },
    //国籍变化
    handleCountryBtn() {
      this.getTable();
      this.getCross();
    },
    //分数排名
    getTable() {
      getTableData({ identity: this.country }).then((res) => {
        this.tableData = res.data.data;
      });
    },
    //横向排名
    getCross() {
      getTableData({ identity: this.country }).then((res) => {
        const teacherName = [];
        const systemScoreList = [];
        const systemName = [];
        const systemScore = [];
        const seriesData = []
        for (let i = 0; i < res.data.data.length; i++) {
          systemScoreList.push(res.data.data[i].systemScoreList);
          teacherName.push(res.data.data[i].teacherName);
        }
        for (let i = 0; i < systemScoreList[0].length; i++) {
          systemName.push(systemScoreList[0][i].system);
        }
        for (let j = 0; j < systemName.length; j++) {
          var arr = [];
          for (let i = 0; i < systemScoreList.length; i++) {
            arr.push(systemScoreList[i][j].score);
          }
          systemScore.push(arr);
        }
        for (let i = 0; i < systemName.length; i++) {
          seriesData.push({
              name: systemName[i],
              type: "bar",
              stack: "total",
              label: {
                show: true,
                textStyle: {
                  fontSize: 8,
                },
              },
              emphasis: {
                focus: "series",
              },
              data: systemScore[0],
            })          
        }
        this.rankingChart = echarts.init(this.$refs.rankingChart);    
          var rankingChartOption = {
          tooltip: {
            trigger: "axis",
            axisPointer: {
              // Use axis to trigger tooltip
              type: "shadow",
            },
          },
          legend: {
            textStyle: {
              //图例字体大小
              fontSize: 8,
            },
            itemHeight: 5,
            itemWidth: 8,
          },
          grid: {
            top: "20%",
            left: "0%",
            bottom: "2%",
            containLabel: true,
          },
          xAxis: {
            type: "value",
            axisLabel: {
              textStyle: {
                fontSize: 8,
              },
            },
          },
          yAxis: {
            type: "category",
            axisLabel: {
              textStyle: {
                fontSize: 8,
              },
            },
            data: teacherName,
          },
          series:seriesData
        };
        this.rankingChart.clear();
        this.rankingChart.setOption(rankingChartOption);
        window.addEventListener("resize", this.screenAdapterRankingChart);
        this.screenAdapterRankingChart();
      });
    },
    //教师指标柱状图
    getTargetingChart() {
      this.targeting = echarts.init(this.$refs.targeting);
      var targetingOption = {
        xAxis: {
          type: "category",
          data: [],
          axisLabel: {
            textStyle: {
              fontSize: 6,
            },
          },
        },
        yAxis: {
          type: "value",
        },

        tooltip: {
          trigger: "axis",
        },
        grid: {
          top: "10%",
          left: "13%",
          right: "4%",
          bottom: "20%",
        },
        series: [
          {
            data: [],
            type: "bar",
          },
        ],
      };
      this.targeting.setOption(targetingOption);
    },
    //指标柱状图选择评测
    clickTerm(id) {
      this.testID = id;
    },
    //指标柱状图选择教师
    clickteacher(id) {
      if (this.testID == null) {
        this.$message.warning("请选择评测名称！");
      } else {
        getTargeting({ eid: this.testID, tid: id })
          .then((res) => {
            const xName = [];
            const score = [];
            for (let i = 0; i < res.data.data.length; i++) {
              xName.push(res.data.data[i].system);
              score.push(res.data.data[i].score);
            }
            this.targeting.setOption({
              xAxis: {
                data: xName,
              },
              series: [
                {
                  data: score,
                },
              ],
            });
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    //遍历对象用于扇形图
    traversal(arr, val) {
      for (let key in val) {
        const m = { name: key, value: val[key] };
        arr.push(m);
      }
    },
    //自适应
    screenAdapterRussianChart1() {
      const titleFontSize = (this.$refs.russianChart.offsetWidth / 100) * 3.6;
      const adapterOption = {
        legend: {
          top: titleFontSize * 5,
          left: titleFontSize * 1,
        },
        series: [
          {
            center: ["70%", "50%"],
            radius: [titleFontSize * 4, titleFontSize * 5.5],
          },
        ],
      };
      this.russianChart1.setOption(adapterOption);
      this.russianChart1.resize();
    },
    screenAdapterRussianChart2() {
      const titleFontSize = (this.$refs.russianChart2.offsetWidth / 100) * 3.6;
      const adapterOption = {
        legend: {
          top: titleFontSize * 5,
          left: titleFontSize * 1,
        },
        series: [
          {
            center: ["70%", "50%"],
            radius: titleFontSize * 5.5,
          },
        ],
      };
      this.russianChart2.setOption(adapterOption);
      this.russianChart2.resize();
    },
    screenAdapterChineseChart1() {
      const titleFontSize = (this.$refs.chineseChart.offsetWidth / 100) * 3.6;
      const adapterOption = {
        legend: {
          top: titleFontSize * 5,
          left: titleFontSize * 1,
        },
        series: [
          {
            center: ["70%", "50%"],
            radius: [titleFontSize * 4, titleFontSize * 5.5],
          },
        ],
      };
      this.chineseChart1.setOption(adapterOption);
      this.chineseChart1.resize();
    },
    screenAdapterChineseChart2() {
      const titleFontSize = (this.$refs.chineseChart2.offsetWidth / 100) * 3.6;
      const adapterOption = {
        legend: {
          top: titleFontSize * 5,
          left: titleFontSize * 1,
        },
        series: [
          {
            center: ["70%", "50%"],
            radius: titleFontSize * 5.5,
          },
        ],
      };
      this.chineseChart2.setOption(adapterOption);
      this.chineseChart2.resize();
    },
    screenAdapterChineseRadar() {
      const titleFontSize =
        (this.$refs.chineseRadarChar.offsetWidth / 100) * 3.6;
      const adapterOption = {
        radar: [
          {
            center: ["50%", "50%"],
            radius: titleFontSize * 5,
          },
        ],
      };
      this.chineseRadarChar.setOption(adapterOption);
      this.chineseRadarChar.resize();
    },
    screenAdapterRussianRadar() {
      const titleFontSize =
        (this.$refs.russianRadarChar.offsetWidth / 100) * 3.6;
      const adapterOption = {
        radar: [
          {
            center: ["50%", "50%"],
            radius: titleFontSize * 5,
          },
        ],
      };
      this.russianRadarChar.setOption(adapterOption);
      this.russianRadarChar.resize();
    },
    screenAdapterRankingChart() {
      const titleFontSize = (this.$refs.rankingChart.offsetWidth / 100) * 3.6;
      const adapterOption = {
        title: {
          x: titleFontSize * 13,
          y: titleFontSize * 10,
          textStyle: {
            fontSize: titleFontSize * 1.5,
          },
        },
      };
      this.rankingChart.setOption(adapterOption);
      this.rankingChart.resize();
    },
    screenAdapterTargeting() {
      const titleFontSize = (this.$refs.targeting.offsetWidth / 100) * 3.6;
      const adapterOption = {
        xAxis: {
          axisLabel: {
            textStyle: {
              fontSize: titleFontSize,
            },
          },
        },
      };
      this.targeting.setOption(adapterOption);
      this.targeting.resize();
    },
    screenAdapterSumPredict() {
      const titleFontSize = (this.$refs.sumPredict.offsetWidth / 100) * 3.6;
      const adapterOption = {
        title: {
          text: "暂无数据",
          x: titleFontSize * 14,
          y: titleFontSize * 4,
          textStyle: {
            fontSize: titleFontSize / 1.1,
          },
        },
      };
      this.sumPredict.setOption(adapterOption);
      this.sumPredict.resize();
    },
  },
  mounted() {
    //基础扇形图
    getBasicData()
      .then((res) => {
        if (res.data.code === 0) {
          const {
            russianTeacherGenderChart,
            russianTeacherTitleChart,
            chineseTeacherGenderChart,
            chineseTeacherTitleChart,
          } = res.data.data;
          const russianGenderArr = [];
          this.traversal(russianGenderArr, russianTeacherGenderChart);
          //俄方扇形1
          this.russianChart1 = echarts.init(this.$refs.russianChart);
          var russianChartOption;
          if (russianGenderArr.length == 0) {
            //暂无数据
            russianChartOption = {
              title: {
                text: "暂无数据",
                x: "center",
                y: "center",
                textStyle: {
                  fontSize: 14,
                  fontWeight: "normal",
                },
              },
            };
          } else {
            russianChartOption = {
              tooltip: {
                trigger: "item",
                confine: true,
              },
              legend: {
                top: "middle",
                left: "right",
                orient: "vertical",
              },
              color: ["rgb(142,207,201)", "rgb(130,176,210)"],
              series: [
                {
                  name: "俄方教师",
                  type: "pie",
                  data: russianGenderArr,
                  center: ["30%", "50%"],
                  avoidLabelOverlap: false,
                  radius: ["50%", "70%"],
                  itemStyle: {
                    borderRadius: 10,
                    borderColor: "#fff",
                    borderWidth: 2,
                  },
                  label: {
                    show: false,
                    position: "center",
                  },
                  labelLine: {
                    show: false,
                  },
                },
              ],
            };
          }
          this.russianChart1.setOption(russianChartOption);
          window.addEventListener("resize", this.screenAdapterRussianChart1);
          this.screenAdapterRussianChart1();

          const russianTitleArr = [];
          this.traversal(russianTitleArr, russianTeacherTitleChart);
          //俄方扇形2
          this.russianChart2 = echarts.init(this.$refs.russianChart2);
          var russianChart2Option;
          if (russianTitleArr.length === 0) {
            russianChart2Option = {
              title: {
                text: "暂无数据",
                x: "center",
                y: "center",
                textStyle: {
                  fontSize: 14,
                  fontWeight: "normal",
                },
              },
            };
          } else {
            russianChart2Option = {
              tooltip: {
                trigger: "item",
                confine: true,
              },
              legend: {
                orient: "vertical",
                left: "right",
                top: "middle",
              },
              series: [
                {
                  name: "俄方教师",
                  type: "pie",
                  radius: "60%",
                  center: ["30%", "50%"],
                  label: {
                    normal: {
                      position: "inner",
                      show: false,
                    },
                  },
                  color: [
                    "rgb(142,207,201)",
                    "rgb(130,176,210)",
                    "rgb(047,127,193)",
                  ],
                  data: russianTitleArr,
                  emphasis: {
                    itemStyle: {
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: "rgba(0, 0, 0, 0.5)",
                    },
                  },
                },
              ],
            };
          }
          this.russianChart2.setOption(russianChart2Option);
          window.addEventListener("resize", this.screenAdapterRussianChart2);
          this.screenAdapterRussianChart2();

          const chineseGenderArr = [];
          this.traversal(chineseGenderArr, chineseTeacherGenderChart);
          //中方扇形1
          this.chineseChart1 = echarts.init(this.$refs.chineseChart);
          var chineseChartOption;
          if (chineseGenderArr.length === 0) {
            chineseChartOption = {
              title: {
                text: "暂无数据",
                x: "center",
                y: "center",
                textStyle: {
                  fontSize: 14,
                  fontWeight: "normal",
                },
              },
            };
          } else {
            chineseChartOption = {
              tooltip: {
                trigger: "item",
                confine: true,
              },
              grid: {
                width: "820px",
                height: "280px",
              },
              legend: {
                top: "middle",
                left: "right",
                orient: "vertical",
              },
              color: ["rgb(255,190,122)", "rgb(250,127,111)"],
              series: [
                {
                  name: "中方教师",
                  type: "pie",
                  data: chineseGenderArr,
                  center: ["30%", "50%"],
                  avoidLabelOverlap: false,
                  radius: ["50%", "70%"],
                  itemStyle: {
                    borderRadius: 10,
                    borderColor: "#fff",
                    borderWidth: 2,
                  },
                  label: {
                    show: false,
                    position: "center",
                  },
                  labelLine: {
                    show: false,
                  },
                },
              ],
            };
          }
          this.chineseChart1.setOption(chineseChartOption);
          window.addEventListener("resize", this.screenAdapterChineseChart1);
          this.screenAdapterChineseChart1();

          const chineseTitleArr = [];
          this.traversal(chineseTitleArr, chineseTeacherTitleChart);
          //中方扇形2
          this.chineseChart2 = echarts.init(this.$refs.chineseChart2);
          var chineseChart2Option;
          if (chineseTitleArr.length === 0) {
            chineseChart2Option = {
              title: {
                text: "暂无数据",
                x: "center",
                y: "center",
                textStyle: {
                  fontSize: 14,
                  fontWeight: "normal",
                },
              },
            };
          } else {
            chineseChart2Option = {
              tooltip: {
                trigger: "item",
                confine: true,
              },
              legend: {
                orient: "vertical",
                left: "right",
                top: "middle",
              },
              series: [
                {
                  name: "中方教师",
                  type: "pie",
                  radius: "60%",
                  center: ["30%", "50%"],
                  label: {
                    normal: {
                      position: "inner",
                      show: false,
                    },
                  },
                  color: [
                    "rgb(255,190,122)",
                    "rgb(255,136,132)",
                    "rgb(250,127,111)",
                  ],
                  data: chineseTitleArr,
                  emphasis: {
                    itemStyle: {
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: "rgba(0, 0, 0, 0.5)",
                    },
                  },
                },
              ],
            };
          }
          this.chineseChart2.setOption(chineseChart2Option);
          window.addEventListener("resize", this.screenAdapterChineseChart2);
          this.screenAdapterChineseChart2();
        } else {
          this.$message.error("获取图表信息错误!");
        }
      })
      .catch((err) => {
        console.log(err);
      });
    //中方雷达图
    this.chineseRadarChar = echarts.init(this.$refs.chineseRadarChar);
    this.chineseRadarCharOption = {
      radar: {
        center: ["50%", "50%"],
        radius: 40,
        indicator: [
          { name: "助学态度", max: 50 },
          { name: "助学效果", max: 50 },
          { name: "思政行为", max: 50 },
        ],
      },
      series: [
        {
          type: "radar",
          data: [
            {
              symbol: "none",
              value: this.chineseRadarArr,
              name: "Allocated Budget",
            },
          ],
        },
      ],
    };
    this.chineseRadarChar.setOption(this.chineseRadarCharOption);
    window.addEventListener("resize", this.screenAdapterChineseRadar);
    this.screenAdapterChineseRadar();

    //俄方雷达图
    this.russianRadarChar = echarts.init(this.$refs.russianRadarChar);
    this.russianRadarCharOption = {
      radar: {
        center: ["50%", "50%"],
        radius: 40,
        indicator: [
          { name: "教学态度", max: 50 },
          { name: "助学态度", max: 50 },
          { name: "教学效果", max: 50 },
          { name: "师生交流", max: 50 },
          { name: "教学能力", max: 50 },
        ],
      },
      series: [
        {
          type: "radar",
          data: [
            {
              symbol: "none",
              value: [],
            },
          ],
        },
      ],
    };
    this.russianRadarChar.setOption(this.russianRadarCharOption);
    window.addEventListener("resize", this.screenAdapterRussianRadar);
    this.screenAdapterRussianRadar();
    //折线图
    this.sumPredict = echarts.init(this.$refs.sumPredict);
    let sumPredictData = [];
    var sumPredictOption;
    if (sumPredictData.length == 0) {
      //暂无数据
      sumPredictOption = {
        title: {
          x: "53%",
          y: "center",
          textStyle: {
            fontSize: 14,
            fontWeight: "normal",
          },
        },
      };
    } else {
      sumPredictOption = {
        xAxis: {
          type: "category",
          data: [
            "2021-2022第一学期测评",
            "2021-2022第二学期测评",
            "2022-2023第一学期测评",
            "2022-2023第二学期测评",
          ],
          axisLabel: {
            interval: 0,
            textStyle: {
              fontSize: 8,
            },
          },
        },
        yAxis: {
          type: "value",
        },
        grid: {
          top: "10%",
          left: "13%",
          right: "0%",
          bottom: "20%",
        },
        series: [
          {
            data: [150, 230, 224, 218],
            type: "line",
          },
        ],
      };
    }
    this.sumPredict.setOption(sumPredictOption);
    window.addEventListener("resize", this.screenAdapterSumPredict);
    this.screenAdapterSumPredict();
    getRedline()
      .then((res) => {
        if (res.data.code === 0) {
          for (let i = 0; i < res.data.data.length; i++) {
            this.redlineArr.push(res.data.data[i].name);
          }
        } else {
          this.$message.error("获取红线名单错误");
        }
      })
      .catch((err) => {
        console.log(err);
      });
    this.getTable();
    //中国老师名单
    getChineseTeacher()
      .then((res) => {
        this.chineseTeacher = res.data.data;
      })
      .catch((err) => {
        console.log(err);
      });
    //俄罗斯老师名单
    getRussianTeacher()
      .then((res) => {
        this.russianTeacher = res.data.data;
        this.teacher = [...this.russianTeacher, ...this.chineseTeacher];
      })
      .catch((err) => {
        console.log(err);
      });
    this.getCross();
    //评测名称
    getTestName()
      .then((res) => {
        if (res.data.code === 0) {
          this.testName = res.data.data;
        }
      })
      .catch((err) => {
        console.log(err);
      });
    //右下角柱状图
    this.getTargetingChart();
    setTimeout(() => {
      getTargeting({ eid: this.testName[0].id, tid: this.teacher[0].id })
        .then((res) => {
          const xName = [];
          const score = [];
          for (let i = 0; i < res.data.data.length; i++) {
            xName.push(res.data.data[i].system);
            score.push(res.data.data[i].score);
            this.targeting.setOption({
              xAxis: {
                data: xName,
              },
              series: [
                {
                  data: score,
                },
              ],
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }, 800);
    window.addEventListener("resize", this.screenAdapterTargeting);
    this.screenAdapterTargeting();
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
  destroyed() {
    window.removeEventListener("resize", this.screenAdapterRussianChart1);
    window.removeEventListener("resize", this.screenAdapterRussianChart2);
    window.removeEventListener("resize", this.screenAdapterChineseChart1);
    window.removeEventListener("resize", this.screenAdapterChineseChart2);
    window.removeEventListener("resize", this.screenAdapterRussianRadar);
    window.removeEventListener("resize", this.screenAdapterChineseRadar);
    window.removeEventListener("resize", this.screenAdapterRankingChart);
    window.removeEventListener("resize", this.screenAdapterTargeting);
    window.removeEventListener("resize", this.screenAdapterSumPredict);
  },
};
</script>

