<template>
  <div class="content">
    <!-- 一级标签的添加功能 -->
    <button class="firstAdd" @click="handleAddFirst">+ 添加</button>
    <!-- 中俄切换 -->
    <div class="check">
      <div class="chinese" @click="checkc()">中</div>
      <div class="russian" @click="checkr()">俄</div>
    </div>
    <!-- 添加的弹窗 -->
    <el-dialog
      class="addDialog"
      title="一级指标名称"
      style="margin-top: 10vh"
      :visible.sync="dialogVisible1"
      width="52%"
      :before-close="handleClose1"
    >
      <el-input placeholder="请输入指标名称" v-model="title"></el-input>
      <el-input
        placeholder="请输入指标英文名称"
        v-model="etitle"
        style="margin-top: 20px"
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel1">取 消</el-button>
        <el-button @click="submit1">确 定 </el-button>
      </span>
    </el-dialog>
    <el-table
      :data="tableData"
      style="width: 75%"
      height="70%"
      row-key="sid"
      border
      lazy
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="parentIndex" label="#" min-width="10%">
        <template slot-scope="scope">
          <span>{{ scope.row.parentIndex }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="指标名称" min-width="30%">
      </el-table-column>
      <el-table-column prop="ename" label="指标英文" min-width="35%">
      </el-table-column>
      <el-table-column label="操作" min-width="25%">
        <template slot-scope="scope">
          <div v-if="!!scope.row.parentIndex">
            <el-button size="mini" @click="handleAdd(scope.row)"
              >添加
            </el-button>
            <el-button size="mini" @click="handleEdit(scope.row)"
              >编辑
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >删除
            </el-button>
          </div>
          <div v-else>
            <el-button size="mini" @click="handleEdit(scope.row)"
              >编辑
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >删除
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加和编辑的弹窗 -->
    <el-dialog
      class="addDialog"
      title="二级指标名称"
      style="margin-top: 10vh"
      :visible.sync="dialogVisible"
      width="52%"
      :before-close="handleClose"
    >
      <el-input placeholder="请输入指标名称" v-model="name"></el-input>
      <el-input
        placeholder="请输入指标英文名称"
        v-model="ename"
        style="margin-top: 20px"
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button @click="submit">确 定 </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  addComment,
  deleteComment,
  editComment,
  getChineseComment,
  getRussianComment,
} from "../utils/commentApi";
export default {
  name: "Comment",
  data() {
    return {
      tableData: [],
      dialogVisible: false, //二级
      dialogVisible1: false, //一级
      name: "", //二级内容
      ename: "", //二级英文内容
      title: "", //一级内容
      etitle: "", //一级英文内容
      haschildID: "", //正在进行添加编辑的数组
      modalType: 0, //0为添加，1为编辑
      updateInfo: {},
      country: 1, //0为俄罗斯，1为中国
    };
  },
  methods: {
    checkc() {
      this.country = 1;
      this.getList();
    },
    checkr() {
      this.country = 0;
      this.getList();
    },
    //删除按钮
    handleDelete(row) {
      this.$confirm("确定要删除这条信息吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        deleteComment({ id: row.sid })
          .then((res) => {
            console.log(res.data);
            this.getList();
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },
    //添加一级按钮
    handleAddFirst() {
      this.dialogVisible1 = true;
    },
    //添加按钮
    handleAdd(row) {
      this.modalType = 0;
      this.dialogVisible = true;
      this.haschildID = row.sid;
    },
    //提交按钮
    submit() {
      if (this.modalType === 0) {
        addComment({
          name: this.name,
          ename: this.ename,
          kind: this.country,
          sid: this.haschildID,
        })
          .then((res) => {
            if (res.data.code === 0) {
              this.getList();
              this.$message.success("添加成功");
            }
          })
          .catch((err) => {
            console.log(err);
          });
        this.handleClose();
      } else {
        editComment({
          id: this.updateInfo.sid,
          cname: this.name,
          ename: this.ename,
        })
          .then((res) => {
            if (res.data.code === 0) {
              console.log(res.data);
              this.getList();
              this.handleClose();
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    submit1() {
      addComment({
        name: this.title,
        ename: this.etitle,
        kind: this.country,
      })
        .then((res) => {
          if (res.data.code === 0) {
            this.getList();
            this.$message.success("添加成功");
            this.handleClose1();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //取消按钮
    cancel() {
      this.handleClose();
    },
    cancel1() {
      this.handleClose1();
    },
    //关闭按钮
    handleClose() {
      this.dialogVisible = false;
      this.name = "";
    },
    handleClose1() {
      this.dialogVisible1 = false;
      this.title = "";
    },
    //编辑按钮
    handleEdit(row) {
      this.modalType = 1;
      this.dialogVisible = true;
      this.name = row.name;
      this.updateInfo = row;
    },
    getList() {
      if (this.country === 1) {
        getChineseComment().then((res) => {
          if (res.data.code === 0) {
            this.tableData = res.data.data;
            this.tableData.forEach((item, index) => {
              item.parentIndex = index + 1;
            });
            console.log(this.tableData);
          }
        });
      } else {
        getRussianComment()
          .then((res) => {
            if (res.data.code === 0) {
              this.tableData = res.data.data;
              this.tableData.forEach((item, index) => {
                item.parentIndex = index + 1;
              });
              console.log(this.tableData);
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
  },
  mounted() {
    this.getList();
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
};
</script>

<style scoped>
.el-table {
  position: absolute;
  top: 130px;
}
.firstAdd {
  position: absolute;
  top: 80px;
  right: 12px;
  width: 80.01px;
  height: 37.5px;
  margin-right: 25px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgba(193, 214, 250, 0.468);
  border: 1.25px solid rgba(193, 214, 250, 0.468);
  border-radius: 6.25px;
}
.check {
  position: absolute;
  width: 80.01px;
  height: 37.5px;
  right: 38px;
  top: 30px;
  box-sizing: border-box;
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
  cursor: pointer;
}
.chinese {
  /* 外教 */
  float: left;
  width: 50%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  color: rgb(42, 96, 189);
  font-family: Noto Sans SC;
  font-size: 18.75px;
  font-weight: 700;
  line-height: 37.5px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  border-top-left-radius: 6.25px;
  border-bottom-left-radius: 6.25px;
}
.russian {
  /* 助教 */
  float: left;
  width: 50%;
  height: 100%;
  background-color: rgb(42, 96, 189);
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 18.75px;
  font-weight: 700;
  line-height: 37.5px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  border-top-right-radius: 6.25px;
  border-bottom-right-radius: 6.25px;
}
</style>