<template>
  <div>
    <input type="text" v-model="input" placeholder="请输入课程名称" />
    <el-button
      class="search"
      slot="append"
      icon="el-icon-search"
      @click="search(input)"
    ></el-button>
    <!-- 头部 -->
    <div class="addButton">
      <button @click="handleAdd">添加课程</button>
      <button @click="handleExcel">Excel导入</button>
    </div>
    <!-- 添加时的弹框 -->
    <el-dialog
      class="addDialog"
      title="课程信息"
      :visible.sync="dialogVisible1"
      width="55%"
      :before-close="handleClose"
    >
      <!-- 用户的所需要填写的表单信息 -->
      <el-form
        ref="form"
        :rules="rules"
        :model="form"
        :inline="true"
        label-width="100px"
      >
        <el-form-item label="课程名称" prop="cname">
          <el-input
            placeholder="请输入课程名称"
            v-model="form.cname"
            :disabled="handleable"
          ></el-input>
        </el-form-item>
        <el-form-item label="课程英文名" prop="ename">
          <el-input
            placeholder="请输入课程英文名"
            v-model="form.ename"
            :disabled="handleable"
          ></el-input>
        </el-form-item>
        <el-form-item label="授课专业" prop="major">
          <el-select
            v-model="form.major"
            placeholder="请选择"
            :disabled="handleable"
          >
            <el-option label="计算机科学与技术" :value="0"></el-option>
            <el-option label="自动化" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="教师" prop="tid">
          <el-select
            multiple
            v-model="form.tid"
            placeholder="请选择"
            size="small"
            @change="changeTeacher"
            clearable
            collapse-tags
          >
            <el-option
              v-for="Item in teacherOptions"
              :key="Item.id"
              :label="Item.name"
              :value="Item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="学期" prop="grade">
          <el-select
            v-model="form.grade"
            placeholder="请选择"
            :disabled="handleable"
          >
            <el-option :value="1" label="第一学期"></el-option>
            <el-option :value="2" label="第二学期"></el-option>
            <el-option :value="3" label="第三学期"></el-option>
            <el-option :value="4" label="第四学期"></el-option>
            <el-option :value="5" label="第五学期"></el-option>
            <el-option :value="6" label="第六学期"></el-option>
            <el-option :value="7" label="第七学期"></el-option>
            <el-option :value="8" label="第八学期"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button @click="submit">确 定 </el-button>
      </span>
    </el-dialog>
    <!-- excel导入弹框 -->
    <el-dialog
      class="addDiaglog"
      title="Excel导入"
      :visible.sync="dialogVisible2"
      width="40%"
    >
      <el-upload
        class="upload-demo"
        action="#"
        :before-upload="beforeUpload"
        accept=".xls, .xlsx"
        :before-remove="beforeRemove"
        :multiple="false"
        :limit="1"
        :on-exceed="handleExceed"
        :file-list="fileList"
      >
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">
          只能上传xls/xlsx文件，且不超过500kb
          <a @click="downloadFile">下载模板</a>
        </div>
      </el-upload>
    </el-dialog>
    <!-- 展示表单 -->
    <div class="common-table">
      <el-table
        height="90%"
        :data="pageTicket"
        style="width: 100%"
        stripe
        empty-text="暂无数据"
      >
        <el-table-column prop="index" label="#" min-width="7%">
          <template slot-scope="scope">
            <span>{{ scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="cname"
          label="课程名称"
          min-width="17%"
        ></el-table-column>
        <el-table-column prop="ename" label="课程英文名" min-width="22%">
        </el-table-column>
        <el-table-column prop="major" label="授课专业" min-width="17%">
          <template slot-scope="scope">
            <span>{{
              scope.row.major == 1 ? "自动化" : "计算机科学与技术"
            }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="teacher" label="授课老师" min-width="17%">
        </el-table-column>
        <el-table-column
          prop="grade"
          label="学期"
          min-width="17%"
        ></el-table-column>
        <el-table-column label="操作" min-width="20%">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >编辑
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 尾部 -->
    <div class="footer">
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentpage"
          :page-size="pagesize"
          layout="total, prev, pager, next, jumper"
          :total="tableData.length"
        >
        </el-pagination>
      </div>
      <button class="reset" @click="reset">重置</button>
    </div>
  </div>
</template>

<script>
import {
  downLoad,
  addCourse,
  editCourse,
  deleteCourse,
  uploadExcel,
  getCourseMessage,
  getChineseTeacherMessage,
  getRussianTeacherMessage,
} from "../utils/classApi";
export default {
  name: "ClassManage",
  data() {
    return {
      input: "",
      //上传文件所用的token
      dialogVisible1: false, //课程弹窗
      dialogVisible2: false, //excel弹窗
      fileList: [],
      form: {
        cname: "",
        ename: "",
        major: "",
        tid: [],
        grade: "",
      },
      modalType: 0, // 0表示新增的弹窗，1表示编辑
      rules: {
        cname: [{ required: true, message: "请输入课程名称" }],
        ename: [{ required: true, message: "请输入课程英文名" }],
        major: [{ required: true, message: "请选择授课专业" }],
        tid: [{ required: true, message: "请选择授课老师", trigger: "change" }],
        grade: [{ required: true, message: "请选择学期" }],
      },
      tableData: [],
      currentpage: 1, //当前所在页默认是第一页
      pagesize: 10, //每页显示多少行数据 默认设置为10
      pageTicket: [], //分页后的当前页数据
      teacherOptions: [], //获取教师的id和名称
      handleable: false, //是否被禁用
    };
  },
  methods: {
    //搜索
    search(input) {
      this.pageTicket = this.tableData.filter((data) => {
        return data.cname === input || data.ename === input;
      });
    },
    handleExcel() {
      this.dialogVisible2 = true;
    },
    beforeUpload: function (file) {
      var fd = new window.FormData();
      fd.append("file", file);
      uploadExcel(fd).then((res) => {
        if (res.data.code == 0) {
          this.$message.success("上传文件成功！");
        } else {
          this.$message.error("上传文件失败！");
          console.log(res.data);
        }
      });
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${
          files.length + fileList.length
        } 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    //弹窗确认事件
    submit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.modalType === 0) {
            addCourse(this.form)
              .then((res) => {
                if (res.data.code == 0) {
                  this.$message.success("添加成功！");
                  this.getList();
                } else {
                  this.$message.error("添加失败！");
                  console.log(res.data);
                }
              })
              .catch((err) => {
                console.log(err);
              });
          } else {
            editCourse(this.form)
              .then((res) => {
                console.log(res);
                if (res.data.code === 0) {
                  this.$message.success("编辑成功！");
                  this.getList();
                } else {
                  this.$message.error("编辑失败");
                  console.log(res.data);
                }
              })
              .catch((err) => {
                console.log(err);
              });
          }
          console.log(this.form, "1111");
          this.dialogVisible1 = false;
          this.$refs.form.resetFields();
        }
      });
    },
    //弹窗取消
    cancel() {
      this.handleClose();
    },
    //弹窗教师选择
    changeTeacher(val) {
      this.form.tid = val;
    },
    //新增课程按钮
    handleAdd() {
      this.modalType = 0;
      this.handleable = false;
      this.dialogVisible1 = true;
    },
    //下载模板
    downloadFile() {
      downLoad().then((res) => {
        console.log(res.data);
        const blob = new Blob([res.data], {
          type: "application/vnd.ms-excel",
        });
        const link = document.createElement("a"); // 生成一个a标签。
        link.href = window.URL.createObjectURL(blob); // href属性指定下载链接
        link.download = "课程下载模板"; // dowload属性指定文件名
        link.click(); // click()事件触发下载
      });
    },
    //关闭课程按钮
    handleClose() {
      this.dialogVisible1 = false;
      this.$refs.form.resetFields();
      // console.log(this.form);
    },
    //删除按钮
    handleDelete(row) {
      this.$confirm("确定要删除这条信息吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        deleteCourse({ id: row.id })
          .then((res) => {
            if (res.data.code === 0) {
              this.$message.success("删除成功！");
              this.tableData = this.tableData.filter((item) => {
                return item.id !== row.id;
              });
              this.getPageInfo();
            } else {
              this.$message.error("删除失败！");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },
    //编辑按钮
    handleEdit(row) {
      this.modalType = 1;
      this.handleable = true;
      this.dialogVisible1 = true;
      this.$nextTick(() => {
        this.form = JSON.parse(JSON.stringify(row));
        (this.form.tid = []), this.form.tid.push(row.tid);
        if (this.form.grade === "第一学期") {
          this.form.grade = 1;
        } else if (this.form.grade === "第二学期") {
          this.form.grade = 2;
        } else if (this.form.grade === "第三学期") {
          this.form.grade = 3;
        } else if (this.form.grade === "第四学期") {
          this.form.grade = 4;
        } else if (this.form.grade === "第五学期") {
          this.form.grade = 5;
        } else if (this.form.grade === "第六学期") {
          this.form.grade = 6;
        } else if (this.form.grade === "第七学期") {
          this.form.grade = 7;
        } else if (this.form.grade === "第八学期") {
          this.form.grade = 8;
        }
        this.form = JSON.parse(JSON.stringify(this.form));
      });
    },
    //获取列表信息
    getList() {
      getCourseMessage()
        .then((res) => {
          if (res.data.code === 0) {
            this.tableData = res.data.data;
            this.getPageInfo();
          } else {
            this.$message.error("获取列表信息错误！");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getPageInfo() {
      //清空pageTicket中的数据
      this.pageTicket = [];
      // 获取当前页的数据
      for (
        let i = (this.currentpage - 1) * this.pagesize;
        i < this.tableData.length;
        i++
      ) {
        //把遍历的数据添加到pageTicket里面
        this.pageTicket.push(this.tableData[i]);
        //判断是否达到一页的要求
        if (this.pageTicket.length === this.pagesize) break;
      }
    },
    handleSizeChange(size) {
      //修改当前每页的数据行数
      this.pagesize = size;
      //数据重新分页
      this.getPageInfo();
    },
    handleCurrentChange(pageNumber) {
      //修改当前的页码
      this.currentpage = pageNumber;
      //数据重新分页
      this.getPageInfo();
    },
    //重置按钮
    reset() {
      this.getList();
      this.input = "";
    },
  },
  mounted() {
    this.getList();
    getChineseTeacherMessage()
      .then((res) => {
        this.teacherOptions = res.data.data;
      })
      .catch((err) => {
        console.log(err);
      });
    getRussianTeacherMessage()
      .then((res) => {
        this.teacherOptions = this.teacherOptions.concat(res.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
};
</script>

<style scoped>
/* 搜索框 */
input {
  position: absolute;
  width: 606.93px;
  height: 37.5px;
  left: 308.15px;
  top: 40.62px;
  box-sizing: border-box;
  background: rgb(247, 247, 247);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
  outline-color: transparent;
  color: rgb(127, 127, 127);
  font-family: Noto Sans SC;
  padding-left: 34.38px;
  line-height: 37.5px;
  font-size: 12px;
}
.search {
  position: absolute;
  background: transparent;
  border: none;
  left: 870.46px;
  top: 40.62px;
  height: 37.5px;
  font-size: 12px;
  border-top-right-radius: 6.25px;
  border-bottom-right-radius: 6.25px;
  color: rgb(127, 127, 127);
}
.search:active {
  color: rgb(127, 127, 127);
  border: none;
  outline: 0;
}
.search:focus,
.search:hover {
  color: rgb(127, 127, 127);
  border: none;
  background-color: transparent;
}
.addButton {
  display: flex;
  position: absolute;
  top: 40.62px;
  left: 951.97px;
  right: 154.39px;
}
.addButton button {
  height: 37.5px;
  margin-right: 25px;
  flex: 0 0 93.76px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgb(42, 96, 189);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
}
/* 弹窗 */
.el-select,
.el-input {
  width: 208px !important;
}
.dialog-footer .el-button {
  background-color: rgb(42, 96, 189);
  color: rgb(255, 255, 255);
}
a {
  float: right;
  border-bottom: 1px dotted red;
  color: rgb(42, 96, 189);
}
/* 展示表格部分 */
.common-table {
  position: relative;
  height: 459px;
  top: 48px;
}
.el-table {
  font-size: 12px;
}
.el-table >>> .el-table__header {
  height: 37.5px !important;
}
.el-table >>> .el-table__cell {
  padding: 0;
}
.el-table >>> .el-table__row {
  height: 37.5px !important;
}
.reset {
  position: absolute;
  width: 60px;
  height: 27px;
  bottom: 35px;
  right: 38px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgb(42, 96, 189);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
}
/* 分页 */
.block {
  margin-top: 18px;
}
.el-pagination {
  text-align: center;
  width: 24rpx;
  height: 24rpx;
  position: absolute;
  left: 22rpx;
  border-left: 4rpx solid #ffffff;
  border-bottom: 4rpx solid #ffffff;
}
.el-pagination::before {
  content: "";
  position: absolute;
  top: -20rpx;
  left: -20rpx;
  bottom: -20rpx;
  right: -20rpx;
}
</style>