import { request } from './baseURL' // 引入刚刚创建的域名文件
import Cookie from 'js-cookie'
export function getPublishList() {
  return request({
    url: '/api/evaluate/list',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'get'
  })
}
//获取中国一级二级指标
export function getChineseComment() {
  return request({
    url: '/api/system/list/china',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'get'
  })
}
//获取俄国一级二级指标
export function getRussianComment() {
  return request({
    url: '/api/system/list/russia',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'get'
  })
}
//获取全部排名
export function getAllranking(data) {
  return request({
    url: '/api/score/get/rank/teacher/total',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'post',
    data: data
  })
}
