<template>
  <div>
    <input type="text" v-model="input" placeholder="请输入评测名称" />
    <el-button
      class="search"
      slot="append"
      icon="el-icon-search"
      @click="search(input)"
    ></el-button>
    <div class="publishButton">
      <button @click="publishTest">发布评测</button>
    </div>
    <!-- 上部分表格展示 -->
    <div class="common-table">
      <el-table
        height="90%"
        :data="pageTicket"
        style="width: 100%"
        stripe
        empty-text="暂无数据"
      >
        <el-table-column prop="index" label="#" min-width="6%">
          <template slot-scope="scope">
            <span>{{ scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="评测名称" min-width="20%">
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间" min-width="13%">
        </el-table-column>
        <el-table-column prop="start_time" label="开始时间" min-width="13%">
        </el-table-column>
        <el-table-column prop="e_time" label="结束时间" min-width="13%">
        </el-table-column>
        <el-table-column prop="status" label="发布状态" min-width="10%">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="rgb(127, 127, 127)"
              :active-value="1"
              :inactive-value="0"
              @change="handleUpdateStatus(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" min-width="25%">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleCheck(scope.row)"
              >查看
            </el-button>
            <el-button size="mini" @click="handleEdit(scope.row)"
              >编辑
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 添加时的弹框 -->
    <el-dialog
      class="addDialog"
      title="评测"
      :visible.sync="dialogVisible"
      width="52%"
      :before-close="handleClose"
    >
      <!-- 用户的所需要填写的表单信息 -->
      <el-form ref="form" :rules="rules" :model="form" :inline="true">
        <el-form-item label="评测名称" prop="name">
          <el-input placeholder="请输入评测名称" v-model="form.name"></el-input>
        </el-form-item>
        <br />
        <el-form-item label="开始时间" prop="start_time">
          <el-date-picker
            v-model="form.start_time"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间" prop="e_time">
          <el-date-picker
            v-model="form.e_time"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button @click="submit">确 定 </el-button>
      </span>
    </el-dialog>
    <!-- 下部分 -->
    <el-card class="box-card" v-show="shown" shadow="never">
      <div slot="header" class="clearfix" style="display: ">
        <span>{{ tableName }}</span>
        <div class="load" @click="downloadFile(eid)">
          <i
            class="el-icon-download"
            style="float: right; padding: 10px 30px; font-size: 19px"
          ></i>
        </div>
      </div>

      <!-- 扇形图 -->
      <div ref="echarts" style="width: 10rem; height: 10rem"></div>
      <!-- 已完成表格 -->
      <el-table :data="dotest" stripe width="30%">
        <el-table-column prop="studentClass" label="班级"> </el-table-column>
        <el-table-column prop="name" label="已评学生"> </el-table-column>
        <el-table-column prop="studentId" label="学号"> </el-table-column>
      </el-table>
      <!-- 未完成表格 -->
      <el-table :data="undotest" stripe width="30%">
        <el-table-column prop="studentClass" label="班级"> </el-table-column>
        <el-table-column prop="name" label="待评学生"> </el-table-column>
        <el-table-column prop="studentId" label="学号"> </el-table-column>
      </el-table>
    </el-card>
    <!-- 尾部 -->
    <div class="footer">
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentpage"
          :page-size="pagesize"
          layout="total, prev, pager, next, jumper"
          :total="tableData.length"
        >
        </el-pagination>
      </div>
      <button class="reset" @click="reset">重置</button>
    </div>
  </div>
</template>

<script>
import {
  addPublish,
  deletePublish,
  editPublish,
  getPublishList,
  getStudentList,
  updateStatus,
  downLoad,
} from "../utils/publishApi";
import * as echarts from "echarts";
export default {
  name: "Publish",
  data() {
    return {
      pageTicket: [], //该页面的显示
      tableData: [], //所有数据
      input: "", //搜索中内容
      dotest: [],
      undotest: [],
      currentpage: 1, //当前所在页默认是第一页
      pagesize: 5, //每页显示多少行数据 默认设置为5
      tableName: "", //下面表格总标题
      eid: "", //导出内容对应的id
      shown: false, //下面表格是否弹出
      dialogVisible: false, //弹窗是否显示
      form: {
        id: "",
        status: 0,
        name: "",
        start_time: "",
        e_time: "",
      },
      modalType: 0,
      rules: {
        name: [{ required: true, message: "请输入姓名" }],
        start_time: [{ required: true, message: "请选择日期" }],
        e_time: [{ required: true, message: "请选择日期" }],
      },
      myChart: null, //echarts存入的对象
    };
  },
  methods: {
    search(input) {
      this.pageTicket = this.tableData.filter((data) => {
        return data.name === input;
      });
    },
    //发布评测
    publishTest() {
      this.dialogVisible = true;
    },
    getList() {
      getPublishList()
        .then((res) => {
          console.log(res.data, "res");
          if (res.data.code === 0) {
            var publishList = res.data.data;
            for (var i = 0; i < publishList.length; i++) {
              var create_time = publishList[i].create_time;
              publishList[i].create_time = create_time.substr(0, 10);
            }
            this.tableData = publishList;
            this.getPageInfo();
          } else {
            this.$message.error("获取列表信息错误！");
            console.log(res, "res");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //图表自适应
    screenAdapter() {
      const titleFontSize = (this.$refs.echarts.offsetWidth / 100) * 3.6;
      const adapterOption = {
        legend: {
          width: titleFontSize * 30,
        },
      };
      this.myChart.setOption(adapterOption);
      this.myChart.resize();
    },
    handleCheck(row) {
      console.log(row);
      this.shown = !this.shown;
      this.tableName = row.name;
      this.eid = row.id;
      getStudentList({ eid: row.id }).then((res) => {
        if (res.data.code === 0) {
          this.dotest = res.data.data.studentDone;
          this.undotest = res.data.data.studentUndone;
          this.myChart = echarts.init(this.$refs.echarts);
          var option = {
            tooltip: {
              trigger: "item",
              confine: true,
            },
            legend: {
              top: "5%",
              left: "center",
            },
            color: ["#0f78f4", "#dd536b"],
            series: [
              {
                name: "评测完成情况",
                type: "pie",
                radius: ["40%", "60%"],
                center: ["50%", "55%"],
                avoidLabelOverlap: false,
                itemStyle: {
                  borderRadius: 10,
                  borderColor: "#fff",
                  borderWidth: 2,
                  top: "100px",
                },
                label: {
                  show: false,
                  position: "center",
                },
                emphasis: {
                  label: {
                    show: false,
                    fontSize: 40,
                    fontWeight: "bold",
                  },
                },
                labelLine: {
                  show: false,
                },
                data: [
                  { value: this.dotest.length || 0, name: "已完成" },
                  { value: this.undotest.length || 0, name: "未完成" },
                ],
              },
            ],
          };
          this.myChart.setOption(option);
          window.addEventListener("resize", this.screenAdapter);
          this.screenAdapter();
        } else {
          // this.$message.error("获取失败");
        }
      });
    },
    handleClose() {
      this.dialogVisible = false;
      this.$refs.form.resetFields();
    },
    submit() {
      console.log(this.form);
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.form.start_time >= this.form.e_time) {
            this.$message.error("请把开始时间设置在结束时间之前");
          } else {
            if (this.modalType === 0) {
              addPublish(this.form)
                .then((res) => {
                  if (res.data.code == 0) {
                    this.$message.success("添加成功！");
                    this.getList();
                  } else {
                    this.$message.error("添加失败！");
                  }
                })
                .catch(() => {});
            } else {
              editPublish(this.form)
                .then((res) => {
                  if (res.data.code === 0) {
                    this.$message.success("编辑成功！");
                    this.getList();
                  } else {
                    this.$message.error("编辑失败");
                  }
                })
                .catch((err) => {
                  console.log(err);
                });
            }
            this.dialogVisible = false;
            this.$refs.form.resetFields();
          }
        }
      });
    },
    cancel() {
      this.handleClose();
    },
    handleEdit(row) {
      this.modalType = 1;
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.form = JSON.parse(JSON.stringify(row));
      });
    },
    handleUpdateStatus(row) {
      updateStatus({ eid: row.id })
        .then((res) => {
          if (res.data.code == 0) {
            this.$message.success("修改成功！");
          } else {
            if (res.data.code === 50000) {
              this.$message.error("信息过期，请您重新登陆！");
            } else {
              this.$message.error(res.data.message)
            }
          }
        })
        .catch((res) => {
          console.log(res);
        });
    },
    handleDelete(row) {
      this.$confirm("确定要删除这条信息吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        deletePublish({ id: row.id })
          .then((res) => {
            if (res.data.code === 0) {
              this.$message.success("删除成功！");
              this.tableData = this.tableData.filter((item) => {
                return item.id !== row.id;
              });
              this.getPageInfo();
            } else {
              this.$message.error("删除失败！");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },
    getPageInfo() {
      //清空pageTicket中的数据
      this.pageTicket = [];
      // 获取当前页的数据
      for (
        let i = (this.currentpage - 1) * this.pagesize;
        i < this.tableData.length;
        i++
      ) {
        //把遍历的数据添加到pageTicket里面
        this.pageTicket.push(this.tableData[i]);
        //判断是否达到一页的要求
        if (this.pageTicket.length === this.pagesize) break;
      }
    },
    handleSizeChange(size) {
      //修改当前每页的数据行数
      this.pagesize = size;
      //数据重新分页
      this.getPageInfo();
    },
    handleCurrentChange(pageNumber) {
      //修改当前的页码
      this.currentpage = pageNumber;
      //数据重新分页
      this.getPageInfo();
    },
    downloadFile(id) {
      downLoad({ eid: id }).then((res) => {
        console.log(res.data);
        const blob = new Blob([res.data], {
          type: "application/vnd.ms-excel",
        });
        const objectUrl = URL.createObjectURL(blob);
        window.location.href = objectUrl;
      });
    },
    //重置按钮
    reset() {
      this.input = "";
      this.getList();
    },
  },
  mounted() {
    this.getList();
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
  destroyed() {
    window.removeEventListener("resize", this.screenAdapter);
  },
};
</script>

<style scoped>
/* 头部 */
input {
  position: absolute;
  width: 725.07px;
  height: 37.5px;
  left: 308.15px;
  top: 40.62px;
  box-sizing: border-box;
  background: rgb(247, 247, 247);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
  outline-color: transparent;
  color: rgb(127, 127, 127);
  font-family: Noto Sans SC;
  padding-left: 34.38px;
  line-height: 37.5px;
  font-size: 12px;
}
.search {
  position: absolute;
  background: transparent;
  border: none;
  left: 980.46px;
  top: 40.62px;
  height: 37.5px;
  line-height: 37.5px;
  font-size: 12px;
  border-top-right-radius: 6.25px;
  border-bottom-right-radius: 6.25px;
  color: rgb(127, 127, 127);
}
.search:active {
  color: rgb(127, 127, 127);
  border: none;
  outline: 0;
}
.search:focus,
.search:hover {
  color: rgb(127, 127, 127);
  border: none;
  background-color: transparent;
}
.publishButton button {
  position: absolute;
  width: 93.76px;
  height: 37.5px;
  top: 40.62px;
  left: 1140.73px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgb(42, 96, 189);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
}
/* 弹窗 */
.el-input {
  width: 208px !important;
}
/* 展示表格部分 */
.common-table {
  position: relative;
  height: 260px;
  top: 48px;
}
.el-table {
  overflow-y: auto;
  font-size: 12px;
  height: 150px;
}
.el-table >>> .el-table__header {
  height: 37.5px !important;
}
.el-table >>> .el-table__cell {
  padding: 0;
}
.el-table >>> .el-table__row {
  height: 37.5px !important;
}
.el-table::before {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0px;
}
/* 下部分 */
.box-card {
  height: 190px;
  margin-top: 15px;
}
.box-card >>> .el-card__header {
  height: 37.5px;
  padding: 0;
}
.clearfix > span {
  display: block;
  float: left;
  margin-left: 360px;
  line-height: 37.5px;
  color: black;
  font-family: Noto Sans SC;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
}
.clearfix .load {
  position: relative;
}
.clearfix .load::before {
  content: "";
