  top: -20px;
  right: -20px;
  bottom: -20px;
  left: -20px;
}
.box-card >>> .el-card__body {
  float: right;
  display: flex;
  width: 100%;
  padding: 0;
}
.box-card .el-table {
  margin-right: 41px;
}
.el-card >>> .el-table__empty-block {
  height: 130px !important;
}
/* 重置按钮 */
.reset {
  position: absolute;
  width: 60px;
  height: 27px;
  bottom: 23px;
  right: 38px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgb(42, 96, 189);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
}
/* 分页 */
.block {
  position: absolute;
  bottom: 23px;
}
/* 选择的按钮 */
.el-switch >>> .el-switch__core {
  width: 35px !important;
  height: 20px;
  /*color:#409EFF;*/
}
/*设置圆*/
.el-switch >>> .el-switch__core::after {
  width: 19px;
  height: 19px;
  margin-top: -1px;
  margin-left: -1px;
}
</style>