import { request } from './baseURL' // 引入刚刚创建的域名文件
import Cookie from 'js-cookie'
//得到已有的评价指标
export function getChineseComment() {
    return request({
        url: '/api/system/list/china',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'get'
    })
}
export function getRussianComment() {
    return request({
        url: '/api/system/list/russia',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'get'
    })
}
//编辑评价信息
export function editComment(data) {
    return request({
        url: '/api/system/update',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//新增评价信息
export function addComment(data) {
    return request({
        url: '/api/system/add',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//删除评价信息
export function deleteComment(data) {
    return request({
        url: '/api/system/delete',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}

