<template>
  <div>
    <el-select
      v-model="test_id"
      placeholder="请选择评测名称"
      class="commentDropdown"
    >
      <el-option
        v-for="item of publishList"
        :key="item.id"
        :value="item.id"
        :label="item.name"
      ></el-option>
    </el-select>
    <el-select
      v-model="first_cateid"
      placeholder="请选择大类"
      @change="handleChange"
      class="firstCate"
    >
      <el-option
        v-for="item of bigList"
        :key="item.sid"
        :value="item.sid"
        :label="item.name"
      ></el-option>
    </el-select>
    <el-select
      v-model="second_cateid"
      placeholder="请选择小类"
      class="secondCate"
    >
      <el-option
        v-for="item of smallList"
        :key="item.sid"
        :value="item.sid"
        :label="item.name"
      ></el-option>
    </el-select>
    <div class="check">
      <div class="chinese" @click="checkc()">助教</div>
      <div class="russian" @click="checkr()">外教</div>
    </div>
    <button class="sort" @click="sort()">筛选</button>
    <button class="export" @click="exportExcel()">导出</button>
    <el-table
      v-if="isSecond"
      :data="pageTicket"
      stripe
      :default-sort="{ prop: 'score', order: 'descending' }"
      ref="multipleTable"
      :height="380"
    >
      <el-table-column prop="index" label="#">
        <template slot-scope="scope">
          <span>{{ scope.$index + 1 }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="姓名"> </el-table-column>
      <el-table-column prop="score" :label="secondTitle"> </el-table-column>
    </el-table>
    <el-table
      v-else
      :data="pageTicket"
      stripe
      :default-sort="{ prop: 'totalScore', order: 'descending' }"
      ref="multipleTable"
      :height="340"
    >
      <el-table-column
        prop="index"
        label="#"
        min-width="30%"
        fixed
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <span>{{ scope.$index + 1 }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        min-width="60%"
        show-overflow-tooltip
        fixed
      >
      </el-table-column>
      <el-table-column
        v-for="item in list"
        :key="item.sid"
        :label="item.name"
        :prop="item.sid.toString()"
        show-overflow-tooltip
        :render-header="renderHeader"
      ></el-table-column>
      <el-table-column
        prop="totalScore"
        label="总分"
        sortable
        :sort-method="
          (a, b) => {
            return a.totalScore - b.totalScore;
          }
        "
      >
      </el-table-column>
    </el-table>
    <!-- 尾部 -->
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentpage"
        :page-size="pagesize"
        layout="total, prev, pager, next, jumper"
        :total="tableData.length"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getExport } from "@/utils/chartApi";
import {
  getPublishList,
  getChineseComment,
  getAllranking,
  getRussianComment,
  getFirstranking,
  getSecondranking,
} from "../utils/scoreApi";
export default {
  name: "Score",
  data() {
    return {
      list: [], //表格的列名
      test_id: "",
      first_cateid: "",
      second_cateid: "",
      allRankingList: [], //排名列表
      publishList: [], //全部测评
      bigList: [], //一级指标
      smallList: [], //二级指标
      tableData: [],
      pagesize: 10, //每页显示多少行数据 默认设置为10
      currentpage: 1, //当前所在页默认是第一页
      country: 1, //0为俄罗斯，1为中国
      pageTicket: [], //分页后的当前页数据
      isSecond: false, //判断是否为筛选了小类
      secondTitle: "", //筛选的小类名称
    };
  },
  methods: {
    //选择了大类才可以选择小类
    handleChange(code) {
      this.smallList = [];
      this.second_cateid = "";
      for (const item of this.bigList) {
        if (code === item.sid) {
          this.smallList = item.children;
        }
      }
    },
    //选择中方
    checkc() {
      this.country = 1;
      this.$message.success("切换成中方成功！");
      this.test_id = "";
      this.first_cateid = "";
      this.second_cateid = "";
      this.getList();
      getChineseComment()
        .then((res) => {
          if (res.data.code === 0) {
            this.bigList = res.data.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 选择俄方
    checkr() {
      this.country = 0;
      this.$message.success("切换成俄方成功！");
      this.test_id = "";
      this.first_cateid = "";
      this.second_cateid = "";
      this.getList();
      getRussianComment()
        .then((res) => {
          if (res.data.code === 0) {
            this.bigList = res.data.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 进行筛选
    sort() {
      if (
        this.test_id !== "" &&
        this.first_cateid !== "" &&
        this.second_cateid === ""
      ) {
        getFirstranking({
          eid: this.test_id,
          identity: this.country,
          sid: this.first_cateid,
        })
          .then((res) => {
            if (res.data.code === 0) {
              this.isSecond = false;
              this.handleTableData(res.data.data);
              this.$nextTick(() => {
                this.$refs.multipleTable.doLayout();
              });
              this.getPageInfo();
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else if (
        this.test_id !== "" &&
        this.first_cateid !== "" &&
        this.second_cateid !== ""
      ) {
        getSecondranking({
          eid: this.test_id,
          identity: this.country,
          secondId: this.second_cateid,
        })
          .then((res) => {
            if (res.data.code === 0) {
              this.tableData = res.data.data;
              this.isSecond = true;
              this.getPageInfo();
              this.secondTitle = res.data.data[0].systemName;
            } else {
              this.$message.error("获取二级指标失败！");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else if (this.test_id === "" || this.first_cateid === "") {
        this.$message.warning("请选择评测名称和大类名称！");
      }
    },
    //导出
    exportExcel() {

      getExport({
        identity: this.country,
        eid: this.test_id,
        sid: this.first_cateid,
        secondId: this.second_cateid,
      }).then((res) => {
        console.log(res.data);
        const blob = new Blob([res.data], {
          type: "application/vnd.ms-excel",
        });
        const link = document.createElement("a"); // 生成一个a标签。
        link.href = window.URL.createObjectURL(blob); // href属性指定下载链接
        link.download = "教师分数排名"; // dowload属性指定文件名
        link.click(); // click()事件触发下载
      });
    },
    // 页面
    getPageInfo() {
      //清空pageTicket中的数据
      this.pageTicket = [];
      // 获取当前页的数据
      for (
        let i = (this.currentpage - 1) * this.pagesize;
        i < this.tableData.length;
        i++
      ) {
        //把遍历的数据添加到pageTicket里面
        this.pageTicket.push(this.tableData[i]);
        //判断是否达到一页的要求
        if (this.pageTicket.length === this.pagesize) break;
      }
    },
    handleSizeChange(size) {
      //修改当前每页的数据行数
      this.pagesize = size;
      //数据重新分页
      this.getPageInfo();
    },
    handleCurrentChange(pageNumber) {
      //修改当前的页码
      this.currentpage = pageNumber;
      //数据重新分页
      this.getPageInfo();
    },
    //对后端传来的数据进行二次处理
    handleTableData(data) {
      this.allRankingList = data;
      this.list = this.allRankingList[0].scoreList;
      this.tableData = [];
      for (var i = 0; i < this.allRankingList.length; i++) {
        var item = {};
        for (var j = 0; j < this.allRankingList[i].scoreList.length; j++) {
          item[this.allRankingList[i].scoreList[j].sid] =
            this.allRankingList[i].scoreList[j].score.toFixed(2);
        }
        item["name"] = this.allRankingList[i].name;
        item["totalScore"] = this.allRankingList[i].totalScore.toFixed(2);
        this.tableData.push(item);
      }
    },
    // table数据
    getList() {
      getAllranking({ identity: this.country }).then((res) => {
        if (res.data.code === 0) {
          this.handleTableData(res.data.data);
          this.isSecond = false;
          this.$nextTick(() => {
            this.$refs.multipleTable.doLayout();
          });
          this.getPageInfo();
        }
      });
    },
    // table表头处理
    renderHeader(h, { column, $index }) {
      let realWidth = 0;
      let span = document.createElement("span");

      span.style.display = "inline-block";
      span.innerText = column.label;
      document.body.appendChild(span);

      realWidth = span.clientWidth;
      column.minWidth = realWidth;

      document.body.removeChild(span);
      return h("span", {}, [column.label]);
    },
  },
  mounted() {
    getPublishList()
      .then((res) => {
        if (res.data.code === 0) {
          this.publishList = res.data.data;
        }
      })
      .catch((err) => {
        console.log(err);
      });
    getChineseComment()
      .then((res) => {
        if (res.data.code === 0) {
          this.bigList = res.data.data;
        }
      })
      .catch((err) => {
        console.log(err);
      });
    this.getList();
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
};
</script>

