<template>
  <el-row class="tac">
    <el-col :span="12">
      <el-menu class="el-menu-vertical-demo" :default-active="this.$route.path">
        <img src="../assets/images/logo.png" alt="" />
        <h1>学评教系统</h1>
        <h3>管理端</h3>
        <div class="menu">
          <div class="first-level">
            <el-menu-item
              :index="item.path"
              v-for="item in noChildren"
              :key="item.name"
              @click="clickMenu(item)"
            >
              <i :class="`el-icon-${item.icon}`"></i>
              <span slot="title">{{ item.label }}</span>
            </el-menu-item>
          </div>
          <el-submenu
            v-for="item in hasChildren"
            :key="item.label"
            :index="item.label"
          >
            <template slot="title">
              <i :class="`el-icon-${item.icon}`"></i>
              <span slot="title">{{ item.label }}</span>
            </template>
            <el-menu-item-group
              v-for="subitem in item.children"
              :key="subitem.name"
            >
              <el-menu-item @click="clickMenu(subitem)" :index="subitem.path">
                <i
                  :class="`el-icon-${subitem.icon}`"
                  style="color: #244482"
                ></i>
                <span slot="title">{{ subitem.label }}</span>
              </el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </div>
      </el-menu>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "commonAside",
  data() {
    return {
      menu: [
        {
          path: "/home/teacherManage",
          name: "teacherManage",
          label: "教师管理",
          icon: "s-custom",
          url: "TeacherManage.vue",
        },
        {
          path: "/home/studentManage",
          name: "studentManage",
          label: "学生管理",
          icon: "user",
          url: "StudentManage.vue",
        },
        {
          path: "/home/classManage",
          name: "classManage",
          label: "课程管理",
          icon: "reading",
          url: "ClassManage.vue",
        },
        {
          path: "/home/publish",
          name: "publish",
          label: "发布评测",
          icon: "s-claim",
          url: "Publish.vue",
        },
        {
          path: "/home/comment",
          name: "comment",
          label: "评价体系",
          icon: "star-off",
          url: "Comment.vue",
        },
        {
          path: "/home/opinion",
          name: "opinion",
          label: "意见反馈",
          icon: "message",
          url: "Opinion.vue",
        },
        {
          label: "评教结果",
          icon: "collection-tag",
          children: [
            {
              path: "/home/charts",
              name: "charts",
              label: "图表展示",
              icon: "pie-chart",
              url: "Charts.vue",
            },
            {
              path: "/home/score",
              name: "score",
              label: "分数排名",
              icon: "s-data",
              url: "Score.vue",
            },
          ],
        },
      ],
    };
  },
  computed: {
    noChildren() {
      return this.menu.filter((item) => !item.children);
    },
    hasChildren() {
      return this.menu.filter((item) => item.children);
    },
  },
  methods: {
    //点击菜单
    clickMenu(item) {
      console.log(item, "item");
      this.$router.push(item.path);
    },
  },
};
</script>

